import { useState, useEffect } from "react";
import {
  useIsAuthenticated,
  useMsal,
  useMsalAuthentication,
} from "@azure/msal-react";
import { InteractionType } from "@azure/msal-browser";
import { loginRequest } from "./authConfig";

// Gets Spaceweb's context provider
import SpacewebProvider from "@sprinklrjs/spaceweb/spacewebProvider";
// Import css variables, which can be used in stylesheet.
import "@sprinklrjs/spaceweb-themes/space/themeVars.min.css";
import "@sprinklrjs/spaceweb-themes/utilities.min.css";
// normalize all styles that browser has to offer to make sure consistent experience across the browser
import "@sprinklrjs/spaceweb-themes/styles/normalize.min.css";
// import some global styles common in sprinklr, like font sizes of header tags, styles of anchor tag etc
import "@sprinklrjs/spaceweb-themes/styles/globals.min.css";

import { Direction, ThemeModule } from "@sprinklrjs/spaceweb-themes/types";
import light from "@sprinklrjs/spaceweb-themes/space/light";
import dark from "@sprinklrjs/spaceweb-themes/space/dark";

import { Loader } from "@sprinklrjs/spaceweb/loader";

import * as microsoftTeams from "@microsoft/teams-js";
import checkInTeams from "./utilities/checkInTeams";

function App() {
  const msalAuthenticationResult = useMsalAuthentication(
    InteractionType.Silent,
    loginRequest
  );

  const [account, setAccount] = useState();
  const msal = useMsal();
  const isAuthenticated = useIsAuthenticated();

  useEffect(() => {
    if (isAuthenticated) {
      setAccount(msal.accounts[0]);
    } else {
    }
  }, [msalAuthenticationResult, isAuthenticated, setAccount, msal]);

  const [state, setState] = useState({
    theme: "light",
    direction: "ltr",
  });

  if (checkInTeams()) {
    console.log("Hello!!!!");
  }

  if (!isAuthenticated) {
    return (
      <SpacewebProvider
        direction={state.direction}
        theme={state.theme === "light" ? light : dark}
      >
        <div>
          <Loader size={24} />
        </div>
      </SpacewebProvider>
    );
  } else {
    return (
      <SpacewebProvider
        direction={state.direction}
        theme={state.theme === "light" ? light : dark}
      >
        <div>
          <div>Hello {account?.name} </div>
        </div>
      </SpacewebProvider>
    );
  }
}

export default App;
