// import "core-js/stable";
import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";

// MSAL imports
import { PublicClientApplication, EventType, msal } from "@azure/msal-browser";
import { MsalProvider } from "@azure/msal-react";
import { msalConfig } from "./authConfig";

import { microsoftTeams, Context } from "@microsoft/teams-js";

const msalInstance = new PublicClientApplication(msalConfig);

// Default to using the first account if no account is active on page load
if (
  !msalInstance.getActiveAccount() &&
  msalInstance.getAllAccounts().length > 0
) {
  // Account selection logic is app dependent. Adjust as needed for different use cases.

  msalInstance.setActiveAccount(msalInstance.getAllAccounts()[0]);
}

// This will update account state if a user signs in from another tab or window
msalInstance.enableAccountStorageEvents();

msalInstance.addEventCallback((event) => {
  if (event.eventType === EventType.LOGIN_SUCCESS && event.payload.account) {
    const account = event.payload.account;
    msalInstance.setActiveAccount(account);
  }
});

const root = ReactDOM.createRoot(document.getElementById("root"));
// root.render(

// <App msal={msalInstance} />
// );

root.render(
  <MsalProvider instance={msalInstance}>
    <App />
  </MsalProvider>
);
